    };
  },
  created () { },
  async mounted () {
    if (this.$route.name == 'labelInttranslation') {
      await this.getList()
      await this.addList()
    }
  },
  methods: {
    async getList () {
      const list = await getLabelList()
      if (list.code == 200) {
        this.list = list.data
      }
    },
    async addList () {
      const res = await getAddList()
      let internation_data_type = res.data.internation_data_type
      let client_type = res.data.client_type
      let version = res.data.version
      client_type.forEach(i => {
        i.children = version
      })
      internation_data_type.forEach(i => {
        i.children = client_type
      })
      const arr = internation_data_type.map(i => {
        return {
          icon: 'arrow_left',
          level: 1,
          value: i.value,
          key: i.key,
          children: i.children.map(j => {
            return {
              icon: 'arrow_left',
              svgName: j.value,
              level: 2,
              parentValue: i.value,
              parentKey: i.key,
              value: j.value,
              key: j.key,
              children: j.children.map(k => {
                return {
                  parentValue: j.value,
                  parentKey: j.key,
                  grandParentKey: i.key,
                  grandParentValue: i.value,
                  value: k.value,
                  key: k.key
                }

              })
            }
          })
        }
      })
      this.level1 = arr
    },
    turnPage (val) {
      const name = val.grandParentKey + '-' + val.parentKey + '-' + val.key
      const value = val.grandParentValue + '---' + val.parentValue + '---' + val.value

      // console.log(name)
      // console.log(value)
      // return
      this.$store.commit('set_uuid', value)
      this.$store.commit('set_subPage_name', name)

      this.$router.push({
        name: 'detaillabelInttranslation',
        params: {
          uuid: value,
          name
        }
      })
    }
  },
  computed: {},
  watch: {
    async '$route' (val) {
      if (val.name == 'labelInttranslation') {
        await this.getList()
        await this.addList()
      }
    }
  },
};
</script>

<style scoped>
.addBtn {
  position: absolute;
  top: -55px;
  right: 10px;
}
</style>
